
.buttons-line-container {
    margin: 100px auto 0;
    width: 100vw;
    transition: .5s;
    background-color: #3f5866;
}

.buttons-line-container-smaller {
    margin: 0;
    width: 100vw;
    transition: .5s;
    background-image: 
        linear-gradient(
            to right, 
            #f0f0df 0%, 
            #000000 100%
            );    
    height: 60px;
}


.Home-content-name {
    transition: 0.5s;
    position: fixed;
    height: 100px;
    margin-top: -10px;
    background-color: #3f5866;
}

.Home-content-name-smaller {
    margin-left: auto;
    margin-right: auto;
    transition: 0.5s;
    height: 35px;
    width: 100vw;
    position: fixed;
}


.Home-header{
    background-size: cover;
    background-color: rgb(36, 11, 11);
    margin-left: auto;
    margin-right: auto;
    transition: 0.5s;
    position: fixed;
}

.Home-header-remove{
    transition: 0.5s;
    background-color: rgb(255, 255, 255);
    position: fixed;
}

.home-header-text{
    font-size: 80px;
    grid-area: home-header-text;
    width: 100vw;
    margin: -90px auto 0;
    font-family: "Playfair Display";
}

.Home-nav-buttons-after-small span {
    align-items: center;
    background: #3f5866;
    display: inline-block;
    height: 100%;
    transition: background .5s ease;
    width: 100%;
}

.Home-nav-buttons{
    background-image: 
        linear-gradient(
            to top, 
            #bdd3de 0%, 
            #3f5866 100%
            );    
    color: rgb(22, 21, 21);
    cursor: pointer;
    height: 45px;
    width: 65px;
    margin: -30px 0 0 600px;
    border: none;
    font-style: italic;
    font-size: 25px;
    grid-area: buttons;
    padding: 0 !important;
    z-index: 0;
    position: relative; 
}

.Home-nav-buttons:hover {
    color: #ff8f00;
}


.Home-nav-buttons span {
	align-items: center;
	background: #3f5866;;
	display: inline-block;
	height: 100%;
	transition: background .5s ease;
	width: 100%;
}

.Home-nav-buttons:hover span {
	background: transparent;
} 

.Home-nav-buttons-after{
    background-image: 
        linear-gradient(
            to top, 
            #bdd3de 0%, 
            #3f5866 100%
            );    
    color: rgb(22, 21, 21);
    cursor: pointer;
    height: 45px;
    width: 100px;
    margin-top: -30px;
    border: none;
    font-style: italic;
    font-size: 25px;
    padding: 0 !important;
    z-index: 0;
    position: relative; 
}

.Home-nav-buttons-after:hover {
    color: #ff8f00;
}


.Home-nav-buttons-after span {
	align-items: center;
	background: #3f5866;;
	display: inline-block;
	height: 100%;
	transition: background .5s ease;
	width: 100%;
}

.Home-nav-buttons-after:hover span {
	background: transparent;
} 

